import { Observer, observer } from 'mobx-react';
import React, { useEffect } from 'react';
import styled from 'styled-components'
import { PlannerViewModel } from './PlannerViewModel';
import CourseGroupView from './CourseGroupView/CourseGroupView';
import YearView from './YearView/YearView';
import { CourseModel } from '../../models/CourseModel';
import ContextMenu from "./components/ContextMenu/ContextMenuView"
import RegionSelect from './components/RegionSelect/RegionSelectView';
import Dragging from './components/Dragging/DraggingView';
import { wasToggleInSelectionGroupKeyUsed } from "./utils"

const Container = styled.div`
  display: flex;
  flex-direction: row;
`

const YearPlanner = styled.div`
  display: inherit;
  overflow: auto;
`

const Planner = observer(({ plannerViewModel }: { [key: string]: PlannerViewModel }) => {
  return <Observer>
    {() => {
      const plannerState = plannerViewModel.plannerState;

      useEffect(() => {
        window.addEventListener("keydown", onWindowKeyDown);
        window.addEventListener("dragenter", onDragEnter);
        window.addEventListener("dragexit", onDragExit);
        window.addEventListener("mouseup", onMouseUp);

        return () => {
          window.removeEventListener("keydown", onWindowKeyDown);
          window.removeEventListener("dragenter", onDragEnter);
          window.removeEventListener("dragexit", onDragExit);
          window.removeEventListener("mouseup", onMouseUp);
        }
      });

      // Drag region selection
      const draggableItemsRef = React.useRef(new Map<CourseModel, HTMLDivElement>());
      const [isRegionSelecting, setIsRegionSelecting] = React.useState(false);

      const registerDraggable = (courseModel: CourseModel, ref: React.MutableRefObject<HTMLDivElement | null | undefined>): (() => void) => {
        if (!ref.current) return () => { };
        draggableItemsRef.current.set(courseModel, ref.current);

        return () => {
          draggableItemsRef.current.delete(courseModel);
        };
      }

      // Dragging
      const [isDragging, setIsDragging] = React.useState(false);

      // Multi select
      const onWindowKeyDown = (event: KeyboardEvent) => {
        if (event.defaultPrevented) return;

        if (event.key === "Escape") plannerViewModel.unselectAllCourses();
      }

      const onMouseUp = (event: MouseEvent) => {
        if (isContextOpen) return;

        if (isDragging) return;

        event.preventDefault();

        if (isRegionSelecting) {
          setIsRegionSelecting(false);
          return;
        }

        if (event.button !== 0) return;
        if (wasToggleInSelectionGroupKeyUsed(event)) return;

        plannerViewModel.unselectAllCourses();
      }

      const onDragEnter = (event) => {
        event.preventDefault();
      }

      const onDragExit = (event) => {
        event.preventDefault();
      }

      const onCourseClick = (course: CourseModel, event: MouseEvent) => {
        if (event.defaultPrevented) return;

        if (event.button !== 0) return;

        if (isRegionSelecting) return;

        event.preventDefault();
        if (isContextOpen) return;

        if (wasToggleInSelectionGroupKeyUsed(event)) {
          plannerViewModel.toggleSelectionInGroup(course);
          return;
        }

        plannerViewModel.toggleSelection(course);
      }

      // Context menu
      const [isContextOpen, setIsContextOpen] = React.useState(false);

      const onCourseContextMenu = (course: CourseModel, event: MouseEvent) => {
        if (event.defaultPrevented) return;

        if (plannerViewModel.selectedCourses.includes(course)) return;

        plannerViewModel.toggleSelection(course);
      }

      return (
        <React.Fragment>
          <RegionSelect
            plannerViewModel={plannerViewModel}
            draggableItemsMap={draggableItemsRef.current}
            setIsRegionSelecting={setIsRegionSelecting}
          />
          <ContextMenu
            plannerViewModel={plannerViewModel}
            isContextOpen={isContextOpen}
            setIsContextOpen={setIsContextOpen}
          />
          <Dragging
            plannerViewModel={plannerViewModel}
            setIsDragging={setIsDragging}
          >
            {
              <CourseGroupView
                horizontal={true}
                key={plannerState.completedGroup.id}
                id={plannerState.completedGroup.id}
                title={plannerState.completedGroup.title}
                term={null}
                courses={plannerState.completedGroup.courses}
                currentDraggableProps={plannerViewModel.currentDraggableProps}
                selectedCourses={plannerViewModel.selectedCourses}
                draggingCourseId={plannerViewModel.draggingCourseId}
                onCourseClick={onCourseClick}
                onCourseContextMenu={onCourseContextMenu}
                isRegionSelecting={isRegionSelecting}
                registerDraggableRef={registerDraggable}
              />
            }
            <Container>
              {
                <CourseGroupView
                  horizontal={false}
                  key={plannerState.plannedGroup.id}
                  id={plannerState.plannedGroup.id}
                  title={plannerState.plannedGroup.title}
                  term={null}
                  courses={plannerState.plannedGroup.courses}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  selectedCourses={plannerViewModel.selectedCourses}
                  draggingCourseId={plannerViewModel.draggingCourseId}
                  onCourseClick={onCourseClick}
                  onCourseContextMenu={onCourseContextMenu}
                  isRegionSelecting={isRegionSelecting}
                  registerDraggableRef={registerDraggable}
                />
              }
              <YearPlanner>
                {
                  plannerState.yearGroups.map(yearGroup => {
                    return <YearView
                      id={yearGroup.id}
                      title={yearGroup.title}
                      year={yearGroup.year}
                      courseGroups={yearGroup.courseGroups}
                      currentDraggableProps={plannerViewModel.currentDraggableProps}
                      selectedCourses={plannerViewModel.selectedCourses}
                      draggingCourseId={plannerViewModel.draggingCourseId}
                      onCourseClick={onCourseClick}
                      onCourseContextMenu={onCourseContextMenu}
                      isRegionSelecting={isRegionSelecting}
                      registerDraggableRef={registerDraggable}
                    />
                  })
                }
              </YearPlanner>
            </Container>
            {
              plannerViewModel.currentDraggableProps.isDragging
                ?
                <CourseGroupView
                  horizontal={true}
                  key={"trash"}
                  id={"trash"}
                  title={"Remove"}
                  term={null}
                  courses={[]}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  selectedCourses={plannerViewModel.selectedCourses}
                  draggingCourseId={plannerViewModel.draggingCourseId}
                  onCourseClick={onCourseClick}
                  onCourseContextMenu={onCourseContextMenu}
                  isRegionSelecting={isRegionSelecting}
                  registerDraggableRef={registerDraggable}
                />
                : <></>
            }
          </Dragging>
        </React.Fragment>
      )
    }}
  </Observer >
})

export default Planner;
