import React from "react";
import { Box, boxesIntersect, useSelectionContainer } from "react-drag-to-select";
import { CourseModel } from "../../../../models/CourseModel";
import { PlannerViewModel } from "../../PlannerViewModel";
import { wasToggleInSelectionGroupKeyUsed } from "../../utils";

interface RegionSelectProps {
  plannerViewModel: PlannerViewModel,
  setIsRegionSelecting: (b: boolean) => void;
  draggableItemsMap: Map<CourseModel, HTMLDivElement>,
}

const RegionSelect = (props: RegionSelectProps) => {
  React.useEffect(() => {
    window.addEventListener("mousedown", onMouseDown);
    window.addEventListener("mouseup", onMouseUp);

    return () => {
      window.removeEventListener("mousedown", onMouseDown)
      window.removeEventListener("mouseup", onMouseUp);
    }
  })

  const onMouseDown = (event: MouseEvent) => {
    if (isToggleSelectionKeyUsed) return;
    if (wasToggleInSelectionGroupKeyUsed(event)) setIsToggleSelectionKeyUsed(true)
  }
  const onMouseUp = (event: MouseEvent) => {
    setIsToggleSelectionKeyUsed(false)
  }

  const draggableBoxesRef = React.useRef<[CourseModel, Box][]>([]);

  const [isToggleSelectionKeyUsed, setIsToggleSelectionKeyUsed] = React.useState(false);
  const [currentlySelected, setCurrentlySelected] = React.useState<CourseModel[]>([]);

  const { DragSelection } = useSelectionContainer({
    onSelectionStart: () => {
      props.setIsRegionSelecting(true);
      //setCurrentlySelected(props.plannerViewModel.selectedCourses.slice());
      draggableBoxesRef.current = [...props.draggableItemsMap.entries()].reduce((previous: [CourseModel, Box][], [courseModel, div]): [CourseModel, Box][] => {
        const rect = div.getBoundingClientRect();
        return [
          ...previous,
          [courseModel, rect]
        ];
      }, []);
    },
    onSelectionChange: React.useCallback((box: Box) => {
      if (isToggleSelectionKeyUsed) {
        const currentlyInRegion = draggableBoxesRef.current
          .filter(([courseModel, draggableBox]) => {
            const alreadyInRegion = currentlySelected.includes(courseModel);
            const nowInRegion = boxesIntersect(box, draggableBox);
            console.log(courseModel.id, alreadyInRegion, nowInRegion)
            if (alreadyInRegion) {
              if (!nowInRegion) {
                console.log("1")
                setCurrentlySelected(currentlySelected.filter((courseSelected) => courseSelected.id !== courseModel.id));
                return true;
              }
            } else {
              if (nowInRegion) {
                console.log("2")
                setCurrentlySelected([...currentlySelected, courseModel]);
                return true;
              }
            }
            return false;
          }, [])
          .map(([courseModel, _]) => courseModel);
        console.log(currentlyInRegion);
        currentlyInRegion.forEach((courseModel) => props.plannerViewModel.toggleSelectionInGroup(courseModel));
      } else {
        draggableBoxesRef.current
          .filter(([courseModel, draggableBox]) => {
            const isSelected: boolean = props.plannerViewModel.selectedCourses.includes(courseModel);
            const isIntersecting: boolean = boxesIntersect(box, draggableBox);

            return (isSelected !== isIntersecting);
          }, [])
          .forEach(([courseModel, _]) => props.plannerViewModel.toggleSelectionInGroup(courseModel));
      }
    }, [isToggleSelectionKeyUsed, currentlySelected]),
    onSelectionEnd: () => {
      draggableBoxesRef.current = [];
      setCurrentlySelected([]);
    }
  });

  return (<DragSelection />)
}

export default RegionSelect;