import { observer, Observer } from "mobx-react";
import React from "react";
import { Popup, Menu, MenuItemProps } from "semantic-ui-react";
import { CourseModel, CourseStatus } from "../../../../models/CourseModel";
import { PlannerViewModel } from "../../PlannerViewModel";

interface ContextMenuProps {
  plannerViewModel: PlannerViewModel;
  isContextOpen: boolean;
  setIsContextOpen: React.Dispatch<React.SetStateAction<boolean>>
}

const ContextMenu = observer((props: ContextMenuProps) => {
  React.useEffect(() => {
    window.addEventListener("contextmenu", onWindowContextMenu);

    return () => {
      window.removeEventListener("contextmenu", onWindowContextMenu);
    }
  })

  const contextRef = React.useRef<any>();

  const [menuItems, setMenuItems] = React.useState<MenuItemProps[]>([]);

  const getContextMenuItems = (course?: CourseModel): MenuItemProps[] => {
    const areMultipleItems = props.plannerViewModel.selectedCourses.length > 1;
    const menuItems: { [key: string]: MenuItemProps } = {
      remove: {
        key: 'remove',
        content: `Remove ${areMultipleItems ? "all courses" : "course"}`,
        icon: 'delete calendar',
        onClick: () => {
          const moveSelectedToRemoved = () => {
            props.plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setUnplanned());
            props.plannerViewModel.unselectAllCourses();
          }
          if (!course) {
            moveSelectedToRemoved();
          } else {
            if (props.plannerViewModel.selectedCourses.includes(course)) moveSelectedToRemoved();
            course.setUnplanned();
          }
        }
      },
      plan: {
        key: 'plan',
        content: `Move ${areMultipleItems ? "all courses" : "course"} to planned`,
        icon: 'bookmark',
        onClick: () => {
          const moveSelectedToPlanned = () => {
            props.plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setPlanned());
          }
          if (!course) {
            moveSelectedToPlanned();
          } else {
            if (props.plannerViewModel.selectedCourses.includes(course)) moveSelectedToPlanned();
            course.setPlanned();
          }
        }
      },
      complete: {
        key: 'complete',
        content: `Move ${areMultipleItems ? "all courses" : "course"} to completed`,
        icon: 'checked calendar',
        onClick: () => {
          const moveSelectedToCompleted = () => {
            props.plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setCompleted());
          }
          if (!course) {
            moveSelectedToCompleted();
          } else {
            if (props.plannerViewModel.selectedCourses.includes(course)) moveSelectedToCompleted();
            course.setCompleted();
          }
        }
      },
    };
    if (course) {
      if (course.courseStatus === CourseStatus.UNPLANNED) {
        const { remove, ...items } = menuItems;
        return Object.values(items);
      } else if (course.courseStatus === CourseStatus.PLANNED) {
        const { plan, ...items } = menuItems;
        return Object.values(items);
      } else if (course.courseStatus === CourseStatus.COMPLETED) {
        const { complete, ...items } = menuItems;
        return Object.values(items);
      }
    }
    return Object.values(menuItems);
  }

  const openContext = (event: MouseEvent) => {
    contextRef.current = {
      getBoundingClientRect: () => ({
        left: event.clientX,
        top: event.clientY,
        right: event.clientX + 1,
        bottom: event.clientY + 1,
        height: 0,
        width: 0,
      }),
    }
    props.setIsContextOpen(true);
  }

  const onWindowContextMenu = (event: MouseEvent) => {
    if (event.defaultPrevented) return;

    const selectedCourses = props.plannerViewModel.selectedCourses;
    if (selectedCourses.length === 0) {
      // Don't use context when no items selected. Possibly change this.
      return;
    } else if (selectedCourses.length === 1) {
      // If only 1 selected course, show a relevant context menu for that course
      setMenuItems(getContextMenuItems(selectedCourses[0]))
    } else {
      // If more than 1 selected course, show a general context
      setMenuItems(getContextMenuItems());
    }

    event.preventDefault();

    openContext(event);
  }

  return <Observer>
    {() => {
      return (
        <Popup
          basic
          context={contextRef}
          onClose={() => props.setIsContextOpen(false)}
          open={props.isContextOpen}
        >
          <Menu
            items={menuItems}
            onItemClick={(e) => {
              e.preventDefault();
              props.setIsContextOpen(false);
            }}
            secondary
            vertical
          />
        </Popup>
      )
    }}
  </Observer>
})

export default ContextMenu;