import React from "react";
import { DragDropContext, DragStart, DropResult } from "react-beautiful-dnd";
import { CourseModel } from "../../../../models/CourseModel";
import { ICourseGroup, PlannerViewModel } from "../../PlannerViewModel";

interface DraggingProps extends React.PropsWithChildren<any> {
  plannerViewModel: PlannerViewModel
  setIsDragging: React.Dispatch<React.SetStateAction<boolean>>

}

const Dragging = (props: DraggingProps) => {
  const courseState = props.plannerViewModel.courseState;

  const onDragStart = (initial: DragStart) => {
    props.setIsDragging(true);

    const selected: CourseModel | undefined = props.plannerViewModel.selectedCourses.find(
      (course: CourseModel): boolean => course.id === initial.draggableId,
    );

    if (!selected) props.plannerViewModel.unselectAllCourses();

    props.plannerViewModel.draggingCourseId = initial.draggableId;

    const course = courseState.getCourseById(initial.draggableId);

    if (!course) return;

    // Don't calculate validations if we are multi selecting
    if (props.plannerViewModel.selectedCourses.length > 1) return;

    props.plannerViewModel.currentDraggableProps = {
      ...props.plannerViewModel.currentDraggableProps,
      possibleTerms: props.plannerViewModel.calculateDisplayableTerms().filter(term => {
        return courseState.getMissingRequirementsWhenAssigningCourseToTerm(course, term).length === 0
      }),
      prerequisiteCourseIds: new Set<string>(courseState.getFlattenedPrerequisiteList(course.prerequisites))
    }
  }

  const onDragEnd = (result: DropResult) => {
    const { destination, source } = result;
    if (!destination || result.reason === "CANCEL") {
      props.plannerViewModel.draggingCourseId = undefined;
      return;
    }
    if (!props.plannerViewModel.draggingCourseId) return;

    props.plannerViewModel.selectedCourses.forEach((course: CourseModel) =>
      props.plannerViewModel.setCourseByDroppableId(course, source.droppableId, destination.droppableId)
    );

    const allGroups = props.plannerViewModel.getAllGroups();
    allGroups.forEach((group: ICourseGroup) => {
      group.courses = group.courses.filter((courseInGroup: CourseModel) =>
        !props.plannerViewModel.selectedCourses.find((selectedCourse: CourseModel) =>
          selectedCourse.id === courseInGroup.id
        )
      )
    })
    props.plannerViewModel.spliceByGroupId(destination.droppableId, destination.index, 0, ...props.plannerViewModel.selectedCourses)

    const draggingCourseIsSelected: boolean = !!props.plannerViewModel.selectedCourses.find((courseModel) => {
      return courseModel.id === props.plannerViewModel.draggingCourseId;
    });
    if (!draggingCourseIsSelected) {
      const courseModel = courseState.getCourseById(props.plannerViewModel.draggingCourseId);
      if (courseModel) {
        props.plannerViewModel.setCourseByDroppableId(courseModel, source.droppableId, destination.droppableId);
        props.plannerViewModel.spliceByGroupId(source.droppableId, source.index, 1);
        props.plannerViewModel.spliceByGroupId(destination.droppableId, destination.index + props.plannerViewModel.selectedCourses.length, 0, courseModel);
      }
    }

    props.setIsDragging(false);

    props.plannerViewModel.draggingCourseId = undefined;

    props.plannerViewModel.clearCurrentDraggableProps()
  }

  const onBeforeCapture = () => {
    props.plannerViewModel.currentDraggableProps = {
      ...props.plannerViewModel.currentDraggableProps,
      isDragging: true,
    }
  }

  return (
    <DragDropContext
      onBeforeCapture={onBeforeCapture}
      onDragStart={onDragStart}
      onDragEnd={onDragEnd}
    >
      {props.children}
    </DragDropContext>
  )
}
export default Dragging;